import videoPreloader from './video-preloader.js';
import { ThreeAnimation } from './three-animation.js';

document.addEventListener('DOMContentLoaded', () => {
    const projectNames = document.querySelectorAll('.projects-names');
    const categories = document.querySelectorAll('.projects-category');
    const loader = document.getElementById('loader');

    // Vérifier si les vidéos sont déjà préchargées
    if (videoPreloader.videosLoaded === 0) {
        loader.style.display = 'flex';
        videoPreloader.preloadVideos().then(() => {
            loader.style.display = 'none';
        }).catch(error => {
            console.error('Error loading videos:', error);
            loader.style.display = 'none';
        });
    }

    // Utility: Apply category-specific gradient
    function getCategoryGradient(category) {
        switch (category) {
            case 'design':
                return 'linear-gradient(129.1deg, #FF153C 14.5%, #FF5387 39.11%, #FF8438 84.05%)';
            case 'development':
                return 'linear-gradient(142deg, #2654EC 7.83%, #5B5BFF 55.07%, #4D00FF 95.88%)';
            case 'generative':
                return 'linear-gradient(50deg, #FF5757 0%, #E743CC 46.31%, #5765FF 95.94%)';
            default:
                return '';
        }
    }

    // Function to reset category styles
    function resetCategories() {
        categories.forEach(cat => {
            cat.classList.remove('hovered');
            Object.assign(cat.style, {
                color: '',
                background: '',
                webkitBackgroundClip: '',
                webkitTextFillColor: '',
                paddingLeft: '4vw'
            });
        });
    }

    // Function to handle hover styles for projects
    function handleProjectHover(project) {
        const category = project.getAttribute('data-category');
        const videoSrc = project.getAttribute('data-src');

        projectNames.forEach(e => e.style.color = 'grey');
        
        // Highlight related category
        categories.forEach(cat => {
            if (cat.getAttribute('data-category') === category) {
                cat.classList.add('hovered');
                Object.assign(cat.style, {
                    color: 'white',
                    paddingLeft: '2vw'
                });
            } else {
                cat.classList.remove('hovered');
            }
        });

        // Apply gradient to the hovered project
        project.style.background = getCategoryGradient(category);
        project.style.webkitBackgroundClip = 'text';
        project.style.webkitTextFillColor = 'transparent';

        // Mettre à jour les couleurs de la grille en fonction de la catégorie
        const categoryMapping = {
            'default': 'default',
            'design': 'design',
            'development': 'development',
            'generative': 'touchdesigner'
        };
        threeAnimation.updateColors(categoryMapping[category]);

        // Mettre à jour la vidéo dans la scène Three.js
        const preloadedVideo = videoPreloader.getVideo(videoSrc);
        if (preloadedVideo) {
            threeAnimation.updateVideoTexture(preloadedVideo.blobUrl);
        } else {
            threeAnimation.updateVideoTexture(videoSrc);
        }
    }

    // Function to reset hover styles
    function resetHover(project) {
        resetCategories();
        projectNames.forEach(e => e.style.color = 'white');
        Object.assign(project.style, {
            background: '',
            webkitBackgroundClip: '',
            webkitTextFillColor: ''
        });
    }

    // Function to display the project preview
    function showProjectPreview(project) {
        // Show the preview window
        // previewWindow.classList.remove('hidden');
    }

    // Function to hide the project preview
    function hideProjectPreview() {
        // previewWindow.classList.add('hidden');
        threeAnimation.updateColors('default');
    }

    // Add hover and leave events to project names
    projectNames.forEach(project => {
        project.addEventListener('mouseenter', () => {
            handleProjectHover(project); // Highlight project category and styles
            showProjectPreview(project); // Show the project preview
        });

        project.addEventListener('mouseleave', () => {
            resetHover(project); // Reset hover styles
            hideProjectPreview(); // Hide the project preview
        });

        // Optional: If you still want click to show preview permanently
        project.addEventListener('click', () => showProjectPreview(project));
    });
    threeAnimation.toggleBokeh(false)
    
});
